import React, { useState } from 'react';
import {
  Container,
  Typography,
  Box,
  Paper,
  Grid,
  Chip,
  Button,
  Divider,
  TextField,
  InputAdornment,
  Card,
  CardContent,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  Search,
  LocalShipping,
  CheckCircle,
  Schedule,
  Receipt,
  KeyboardArrowRight,
} from '@mui/icons-material';
import { Link as RouterLink } from 'react-router-dom';

// Mock order data
const orders = [
  {
    id: 'ORD-2024-001',
    date: '2024-02-15',
    status: 'Delivered',
    total: 159.97,
    items: [
      {
        id: 1,
        name: 'Premium MAGA Cap',
        price: 39.99,
        quantity: 1,
        image: 'https://placehold.co/600x400/FF385C/ffffff?text=Premium+Cap',
      },
      {
        id: 2,
        name: 'Patriot Elite Polo',
        price: 59.99,
        quantity: 2,
        image: 'https://placehold.co/600x400/002868/ffffff?text=Elite+Polo',
      },
    ],
    tracking: 'USP123456789',
  },
  {
    id: 'ORD-2024-002',
    date: '2024-02-10',
    status: 'Processing',
    total: 89.97,
    items: [
      {
        id: 3,
        name: 'Collector\'s Edition Pin Set',
        price: 49.99,
        quantity: 1,
        image: 'https://placehold.co/600x400/FF385C/ffffff?text=Pin+Set',
      },
      {
        id: 4,
        name: 'Heritage Coffee Tumbler',
        price: 39.98,
        quantity: 1,
        image: 'https://placehold.co/600x400/002868/ffffff?text=Tumbler',
      },
    ],
  },
];

const getStatusChipProps = (status) => {
  switch (status.toLowerCase()) {
    case 'delivered':
      return {
        icon: <CheckCircle />,
        color: 'success',
      };
    case 'processing':
      return {
        icon: <Schedule />,
        color: 'warning',
      };
    default:
      return {
        icon: <LocalShipping />,
        color: 'default',
      };
  }
};

const OrderCard = ({ order }) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const statusProps = getStatusChipProps(order.status);

  return (
    <Card sx={{ mb: 2 }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Box>
            <Typography variant="subtitle2" color="text.secondary">
              Order #{order.id}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Placed on {new Date(order.date).toLocaleDateString()}
            </Typography>
          </Box>
          <Chip
            label={order.status}
            icon={statusProps.icon}
            color={statusProps.color}
            size="small"
          />
        </Box>

        <Divider sx={{ my: 2 }} />

        <Grid container spacing={2}>
          {order.items.map((item) => (
            <Grid item xs={12} sm={6} key={item.id}>
              <Box sx={{ display: 'flex', gap: 2 }}>
                <img
                  src={item.image}
                  alt={item.name}
                  style={{
                    width: 80,
                    height: 80,
                    objectFit: 'cover',
                    borderRadius: theme.shape.borderRadius,
                  }}
                />
                <Box>
                  <Typography variant="subtitle2">{item.name}</Typography>
                  <Typography variant="body2" color="text.secondary">
                    Quantity: {item.quantity}
                  </Typography>
                  <Typography variant="body2" color="primary">
                    ${item.price.toFixed(2)}
                  </Typography>
                </Box>
              </Box>
            </Grid>
          ))}
        </Grid>

        <Divider sx={{ my: 2 }} />

        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <Box>
            <Typography variant="subtitle1">
              Total: ${order.total.toFixed(2)}
            </Typography>
            {order.tracking && (
              <Typography variant="body2" color="text.secondary">
                Tracking: {order.tracking}
              </Typography>
            )}
          </Box>
          <Box sx={{ display: 'flex', gap: 1 }}>
            <Button
              variant="outlined"
              size={isMobile ? 'small' : 'medium'}
              startIcon={<Receipt />}
            >
              Invoice
            </Button>
            <Button
              component={RouterLink}
              to={`/order/${order.id}`}
              variant="contained"
              size={isMobile ? 'small' : 'medium'}
              endIcon={<KeyboardArrowRight />}
            >
              Details
            </Button>
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
};

const Orders = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const filteredOrders = orders.filter(order =>
    order.id.toLowerCase().includes(searchQuery.toLowerCase()) ||
    order.items.some(item => item.name.toLowerCase().includes(searchQuery.toLowerCase()))
  );

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper sx={{ p: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Typography variant="h4" gutterBottom={isMobile}>
            My Orders
          </Typography>
          <TextField
            placeholder="Search orders..."
            size="small"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            sx={{ width: isMobile ? '100%' : 300 }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              ),
            }}
          />
        </Box>

        {filteredOrders.length === 0 ? (
          <Box sx={{ textAlign: 'center', py: 4 }}>
            <Typography variant="h6" gutterBottom>
              No orders found
            </Typography>
            <Typography color="text.secondary" paragraph>
              Try adjusting your search or browse our products
            </Typography>
            <Button
              component={RouterLink}
              to="/categories"
              variant="contained"
              color="primary"
            >
              Shop Now
            </Button>
          </Box>
        ) : (
          filteredOrders.map((order) => (
            <OrderCard key={order.id} order={order} />
          ))
        )}
      </Paper>
    </Container>
  );
};

export default Orders;
